<template>
  <div class="order">
    <van-nav-bar title="我的订单" left-text="返回" left-arrow @click-left="back" />
    <div class="list">
      <van-swipe-cell class="list-item" v-for="(item,index) in dingdan" :key="index">
        <div class="clearfix">
            <div @click="comfirm(item,index)" class="wait fr">{{item.isActive ? "确认收货" : "已收货"}}</div>
        </div>
        <div class="content">
          <div class="content-img">
            <img class="auto-img" :src="item.img" alt />
          </div>
          <div class="content-message">
            <p class="text-o">{{item.title}}</p>
            <p class="text-n">{{item.style}}/{{item.size}}</p>

            <div class="product-price">
              <div class="price fl">￥{{item.price}}</div>
              <div class="fr product-count">×{{item.count}}</div>
            </div>
          </div>
        </div>
      </van-swipe-cell>
    </div>
  </div>
</template>
<script>

import "../../assets/css/order.less"
//命名空间用法
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("shopcart");

export default {
  name: "Order",
  data() {
    return {
    };
  },
  created() {
  },
  computed: {
    ...mapState(["dingdan"])
  },
  methods: {
    ...mapMutations(["comfirms"]),
    //跳转到我的页面
    back() {
      this.$router.push({ name: "My" });
    },

    //确认收货
    comfirm(item,index){
      this.comfirms(item.stockId)
      
    }
  }
}
</script>
